h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  color: inherit; }

ul {
  list-style: none; }

.container {
  height: 100%;
  padding: 0 103px;
  width: 100%; }
  @media screen and (max-width: 1365px) {
    .container {
      padding: 0 45px; } }
  @media screen and (max-width: 767px) {
    .container {
      padding: 0 15px; } }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.header {
  font-size: 26px;
  left: 0;
  line-height: 1em;
  padding-top: 30px;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10; }
  @media screen and (max-width: 1365px) {
    .header {
      font-size: 24px;
      padding-top: 40px; } }
  @media screen and (max-width: 767px) {
    .header {
      font-size: 20px;
      padding-top: 20px; } }

.header-wrapper {
  align-items: center;
  display: flex;
  justify-content: space-between; }

.header__phone {
  color: white;
  font-family: "BebasNeue";
  letter-spacing: 0.04em;
  font-weight: bold;
  text-decoration: none; }
  .header__phone:hover {
    text-decoration: underline; }
  @media screen and (max-width: 1365px) {
    .header__phone {
      letter-spacing: 0.03em; } }
  @media screen and (max-width: 767px) {
    .header__phone {
      height: 15px; } }

.header__logo {
  height: 20px; }
  @media screen and (max-width: 1365px) {
    .header__logo {
      height: 19px; } }
  @media screen and (max-width: 767px) {
    .header__logo {
      height: 15px; } }

.fitness {
  background: url("../img/mainBg.jpg") no-repeat 10%;
  background-size: cover;
  color: white;
  height: 658px;
  padding: 51px 0;
  position: relative; }
  @media screen and (max-width: 1365px) {
    .fitness {
      height: 554px;
      padding: 82px 0; } }
  @media screen and (max-width: 767px) {
    .fitness {
      height: 491px; } }

.fitness-wrapper {
  display: flex;
  justify-content: flex-end;
  margin: 0 auto;
  width: 1160px; }
  @media screen and (max-width: 1365px) {
    .fitness-wrapper {
      width: 678px; } }
  @media screen and (max-width: 767px) {
    .fitness-wrapper {
      width: 290px; } }

.fitness__info {
  flex-basis: 350px;
  padding-top: 113px;
  position: relative;
  text-align: right; }
  @media screen and (max-width: 1365px) {
    .fitness__info {
      flex-basis: 280px;
      padding-top: 51px; } }
  @media screen and (max-width: 767px) {
    .fitness__info {
      flex: 100%;
      padding-top: 29px; } }

.fitness__city {
  font-family: "BebasNeue";
  font-size: 49px;
  letter-spacing: 0.025em;
  text-transform: uppercase; }
  @media screen and (max-width: 1365px) {
    .fitness__city {
      font-size: 34px; } }
  @media screen and (max-width: 767px) {
    .fitness__city {
      font-size: 28px; } }

.fitness__title {
  font-family: "BebasNeue";
  font-size: 110px;
  font-weight: bold;
  letter-spacing: 0.03em;
  line-height: 1em;
  margin-top: 4px;
  text-transform: uppercase; }
  @media screen and (max-width: 1365px) {
    .fitness__title {
      font-size: 86px;
      margin: 0; } }
  @media screen and (max-width: 767px) {
    .fitness__title {
      font-size: 78px;
      margin-top: 4px; } }

.fitness__activities {
  font-size: 14px;
  line-height: 1.5em;
  position: absolute;
  right: 264px;
  top: 305px;
  width: 120px; }
  @media screen and (max-width: 1365px) {
    .fitness__activities {
      right: 211px;
      top: 187px; } }
  @media screen and (max-width: 767px) {
    .fitness__activities {
      right: 178px;
      top: 151px; } }

.fitness__buy {
  border: 5px solid white;
  color: white;
  display: inline-block;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 0.5px;
  line-height: 53px;
  margin-top: 60px;
  text-align: center;
  text-transform: uppercase;
  width: 232px; }
  @media screen and (max-width: 1365px) {
    .fitness__buy {
      margin-top: 43px;
      width: 185px; } }
  @media screen and (max-width: 767px) {
    .fitness__buy {
      margin-top: 48px;
      width: 100%; } }

.fitness__banner {
  background-image: url("../img/mainBanner.jpg");
  background-size: cover;
  height: 556px;
  margin-right: 200px;
  margin-left: 64px;
  width: 500px; }
  @media screen and (max-width: 1365px) {
    .fitness__banner {
      background-position: 45%;
      height: 390px;
      margin-right: 0;
      margin-left: 30px;
      width: 324px; } }
  @media screen and (max-width: 767px) {
    .fitness__banner {
      display: none; } }

.fitness__social {
  bottom: 93px;
  font-size: 12px;
  position: absolute;
  right: 103px;
  text-align: end; }
  .fitness__social li {
    margin-top: 20px; }
  @media screen and (max-width: 1365px) {
    .fitness__social {
      display: none; } }

body {
  font-family: "Roboto Condensed", sans-serif; }

h1 {
  font-family: "BebasNeue", sans-serif; }
